.board-read {
    background-color: #141414;
    color: #ffffff;
    padding: 20px;
    font-family: Arial, sans-serif;
  }
  
  .board-content {
    background-color: #2f2f2f;
    border-radius: 8px;
    padding: 20px;
    margin-bottom: 20px;
  }
  
  .board-header {
    margin-bottom: 20px;
    margin-top: 30px;
  }
  
  .board-category {
    background-color: #ffd700;
    color: #000000;
    padding: 5px 10px;
    border-radius: 4px;
    font-size: 14px;
  }
  
  .board-title {
    font-size: 24px;
    margin: 10px 0;
  }
  
  .board-info {
    font-size: 14px;
    color: #aaaaaa;
  }
  
  .board-info span {
    margin-right: 15px;
  }
  
  .board-body {
    font-size: 16px;
    line-height: 1.6;
  }
  
  .comments-section {
    background-color: #2f2f2f;
    border-radius: 8px;
    padding: 20px;
  }
  
  .comment-count {
    font-size: 18px;
    margin-bottom: 15px;
  }
  
  .comment {
    background-color: #3f3f3f;
    border-radius: 4px;
    padding: 10px;
    margin-bottom: 10px;
  }
  
  .comment-author {
    font-weight: bold;
    margin-right: 10px;
  }
  
  .comment-text {
    margin: 5px 0;
  }
  
  .comment-likes {
    font-size: 14px;
    color: #ff4444;
  }